.books {
    display: flex;
    flex-direction: column;
}

.books__text {
    align-self: center;
    margin: 0;
    font-family: 'Inter', 'Arial', sans-serif;
    font-weight: 400;
    font-size: 25px;
    line-height: 28px;
    color: #000;
}

.books__text_paragraph {
    margin-top: 50px;
    color: #69635a;
}

.books__list {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 20px;
    margin: 20px 0 0;
    padding: 0;
    list-style: none;
}

.books__list-item-text {
    box-sizing: border-box;
    display: inline-block;
    margin: 0 2px 0 0;
    padding: 0;
    font-family: 'Inter', 'Arial', sans-serif;
    font-weight: 400;
    font-size: 10px;
    line-height: 12px;
    color: #000;
}

.books__list-item-text::after {
    content: ',';
}

.books__list-item-text:last-of-type::after {
    content: '';
    margin: 0;
}

@media screen and (max-width: 992px) {
    .books__list {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media screen and (max-width: 768px) {
    .books__text {
        font-size: 22px;
        line-height: 24px;
    }
}

@media screen and (max-width: 650px) {
    .books__list {
        grid-template-columns: repeat(2, 1fr);
    }

    .books__list-item-text {
        font-size: 15px;
        line-height: 17px;
    }
}

@media screen and (max-width: 480px) {
    .books__list {
        grid-template-columns: 1fr;
    }
}

